import { TotalAmountsModel } from "./totals.model";
import { TypeOfMedium, OrPreparationReference } from "@models";

export class OrPreparation {
  amount: number;
  amountInWords: string;
  businessStyle: string;
  companyAddress: string;
  companyName: string;
  companyTin: string;
  controlNumber: string;
  fullPaymentOf: string;
  id: number;
  officialReceiptDate: Date;
  officialReceiptNo: string;
  officialReceiptReferences: OrPreparationReference[];
  remarks: string;
  status: string;
  typeOfMedium: TypeOfMedium;
  voucherControlNumber: string;
  filedBirForm: boolean;

  //totals
  totalSales: number;
  totalVat: number;
  totalWithholdingtax: number;
  totalAmountDue: number;

  // Not if BE; for FE only
  officialReceiptReferencesValue: string;

  setTotalAmounts(totals: TotalAmountsModel) {
    this.totalSales = totals.totalSales;
    this.totalAmountDue = totals.totalAmountDue;
    this.totalVat = totals.totalVat;
    this.totalWithholdingtax = totals.totalWithholdingtax;
  }
}
